@import url('https://fonts.googleapis.com/css2?family=Pacifico&display=swap');



body {
  background: url("https://images.pexels.com/photos/941869/pexels-photo-941869.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940");
  background-color: #1d1c1c;
  background-position: center;
  min-height: 100vh;
  background-repeat: repeat;
  background-size: cover;
}

.Content {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin: 5rem auto;
}

form {
  width: 100%;
  min-height: 10vh;
  background-color: #1d1c1ca9;
  margin: 2rem auto;
  border-radius: 10px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 1rem;
}

@media only screen and (max-width: 906px) {
  form {
    flex-direction: column;
    gap: 1.5rem;
    text-align: center;
  }

  .text {
    font-size: 0.8rem;
  }
}

form button {
  background-color: rgb(255, 102, 0) !important;
  color: rgb(255, 255, 255) !important;

}

form input {
  color: rgb(255, 255, 255) !important;
  caret-color: white;
}

.text {
  font-size: 2rem;
  font-weight: 700;
  font-family: 'Pacifico',
    cursive;
  color: rgb(255, 255, 255);
}
